@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

* {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: -moz-none;
  -o-user-select: none;
  user-select: none;
}

body {
  margin: 0px;
  padding: 0px;
  font-weight: 300;
  display: flex;
  overflow: hidden;
}

ion-icon {
  color: var(--white);
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.3);
  height: 10px;
  opacity: 0.5;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.1;
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --black: #000;
  --white: #fff;
  --gray:#bbbbbb;
  --light-gray:rgb(241, 241, 241);
  --bg-color:#121212;
  --blue:#1E1057;
  --play-color:#1ED760;
  --light-black:#171717;
  --player-color:#181818;
  --color-primary: #007bff;
}

.left-side {
  height: 100vh;
  width: 14.375rem;
  background-color: var(--black);
  padding: 0.9375rem;
  color: var(--gray);
  position: fixed;
  top: 0;
  left: 0;
}
@media (max-width: 58.4375em) {
  .left-side {
    display: none;
  }
}
.left-side__file-info {
  width: 100%;
  height: 1.875rem;
  display: flex;
}
.left-side__file-info__dote {
  background-color: var(--white);
  border-radius: 50%;
  width: 0.3125rem;
  height: 0.3125rem;
  margin: 0px 2px 0px 2px;
}
.left-side__icons__main {
  margin: 0px 0px 50px 0px;
}
.left-side__icons__main__ico {
  display: flex;
  align-items: center;
  margin: 10px 0px 10px 0px;
  transition-duration: 0.5s;
}
.left-side__icons__main__ico ion-icon {
  font-size: 1.875rem;
  color: var(--gray);
  cursor: pointer;
}
.left-side__icons__main__ico .white {
  color: white;
}
.left-side__icons__main__ico .main-icon-text {
  font-size: clamp(0.875rem, 1vw, 1.0625rem);
  font-family: "Roboto", sans-serif;
  margin-left: 0.625rem;
  cursor: pointer;
}
.left-side__icons__main__ico:hover {
  color: var(--white);
}
.left-side__icons__main__ico:hover ion-icon {
  color: var(--white);
}
.left-side__icons__other__ico {
  display: flex;
  align-items: center;
  margin: 15px 0px 15px 0px;
  transition-duration: 0.5s;
}
.left-side__icons__other__ico ion-icon {
  font-size: 1.875rem;
  color: var(--gray);
  cursor: pointer;
}
.left-side__icons__other__ico .white-bg {
  background-color: var(--light-gray);
  color: var(--black);
  border-radius: 5px;
}
.left-side__icons__other__ico .graident-bg {
  background-image: linear-gradient(to right, rgb(153, 0, 255), rgb(204, 0, 255));
  font-size: 1.25rem;
  padding: 5px;
  border-radius: 5px;
  color: var(--white);
}
.left-side__icons__other__ico .main-icon-text {
  font-size: clamp(0.875rem, 1vw, 1.0625rem);
  font-family: "Roboto", sans-serif;
  margin-left: 0.625rem;
  cursor: pointer;
}
.left-side__icons__other__ico .downloaded {
  border-radius: 50%;
  font-size: clamp(0.75rem, 1vw, 0.9375rem);
  margin-left: clamp(1.5rem, 1vw, 1.875rem);
  background-color: var(--light-gray);
  color: var(--black);
  padding: 2px;
}
.left-side__icons__other__ico:hover {
  color: var(--white);
}
.left-side__hr {
  border: none;
  background-color: var(--gray);
  height: 1px;
}
.left-side__playlists {
  margin-top: 30px;
}
.left-side__playlists__one-list {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: clamp(0.75rem, 1vw, 0.9375rem);
  margin: 15px 0px 15px 0px;
  color: var(--gray);
  cursor: pointer;
}
.left-side__playlists__one-list:hover {
  color: var(--white);
}
.left-side__playlists .downloaded-playlist, .left-side__playlists .playlist-collection {
  display: flex;
  align-items: center;
  padding: 0;
  margin: -7.5px 0px -7.5px 0px;
}
.left-side__playlists .downloaded-playlist:hover .downloaded, .left-side__playlists .playlist-collection:hover .downloaded {
  background-color: var(--white);
}
.left-side__playlists .downloaded-playlist:hover .look-down, .left-side__playlists .playlist-collection:hover .look-down {
  color: var(--white);
}
.left-side__playlists .downloaded {
  background-color: var(--gray);
  color: var(--black);
  border-radius: 50%;
  margin-left: 57px;
  font-size: clamp(0.75rem, 1vw, 0.9375rem);
}
.left-side__playlists .look-down {
  color: var(--gray);
  font-size: clamp(0.75rem, 1vw, 1.0625rem);
  margin: 10px;
}

.main {
  background-color: var(--bg-color);
  height: calc(100vh - 100px);
  position: fixed;
  top: 0px;
  left: 14.375rem;
  right: 14.375rem;
  overflow-y: scroll;
  overflow-x: hidden;
}
@media (max-width: 58.4375em) {
  .main {
    left: 0;
    right: 0;
  }
}
.main__top-side {
  width: 100%;
  background-image: linear-gradient(to bottom, var(--blue) 0%, var(--bg-color) 80%);
  padding: 1.25rem;
}
.main__top-side__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 0;
}
.main__top-side__top div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.main__top-side__top div ion-icon {
  font-size: 23px;
  background-color: var(--bg-color);
  color: var(--gray);
  margin: 0px 10px 0px 0px;
  cursor: pointer;
  pointer-events: stroke;
  border-radius: 50%;
  padding: 5px;
  opacity: 70%;
}
.main__top-side__top .round {
  background-color: var(--bg-color);
  border-radius: 50px;
  cursor: pointer;
}
.main__top-side__top .round h1 {
  color: var(--white);
  font-family: "Roboto", sans-serif;
  font-size: 0.9375rem;
  margin-left: 10px;
  margin-right: 5px;
}
.main__top-side__top .round img {
  width: 30px;
  border-radius: 50%;
}
.main__top-side__top .round ion-icon {
  font-size: 15px;
}
@keyframes ggg {
  0% {
    transform: translateX(-3px);
  }
  10% {
    transform: translateX(-3px);
  }
  20% {
    transform: translateX(3px);
  }
  30% {
    transform: translateX(-3px);
  }
  40% {
    transform: translateX(3px);
  }
  50% {
    transform: translateX(-3px);
  }
  60% {
    transform: translateX(3px);
  }
  70% {
    transform: translateX(-3px);
  }
  80% {
    transform: translateX(3px);
  }
  90% {
    transform: translateX(-3px);
  }
  100% {
    transform: translateX(3px);
  }
}
.main__top-side .greting {
  color: var(--white);
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  margin-top: 30px;
  font-size: clamp(1.25rem, 2vw, 2.0625rem);
}
.main__top-side__content__row {
  display: flex;
  width: 100%;
  justify-content: space-between;
  flex-wrap: wrap;
}
.main__top-side__content__row div {
  background-color: rgba(148, 148, 148, 0.2);
  font-family: "Roboto", sans-serif;
  font-size: clamp(0.4375rem, 1vw, 0.5625rem);
  color: var(--white);
  display: flex;
  align-items: center;
  width: calc((100vw - 460px) / 3.2);
  min-width: 250px;
  border-radius: 10px;
  margin-top: 30px;
  overflow: hidden;
  cursor: pointer;
  transition-duration: 0.5s;
  position: relative;
}
.main__top-side__content__row div img {
  width: 85px;
  margin-right: 15px;
}
.main__top-side__content__row div:hover {
  background-color: rgba(148, 148, 148, 0.5);
}
.main__top-side__content__row div:hover .play {
  display: block;
}
.main__top-side__content__row .play {
  position: absolute;
  top: 17%;
  right: 5%;
  font-size: 25px;
  padding: 15px;
  color: black;
  background-color: var(--play-color);
  border-radius: 50%;
  display: none;
}
.main__top-side__content__row .play:hover {
  transform: scale(1.05);
}
@media (max-width: 78.6875em) {
  .main__top-side__content__row {
    justify-content: center;
  }
  .main__top-side__content__row div {
    width: calc((100vw - 460px) / 1);
  }
}
@media (max-width: 58.4375em) {
  .main__top-side__content__row div {
    width: calc((100vw - 10px) / 1);
  }
}
.main__content {
  font-family: "Roboto", sans-serif;
  padding: 1.25rem;
  width: 100%;
}
.main__content__top {
  display: flex;
  justify-content: space-between;
}
.main__content__top h1 {
  color: var(--white);
  font-size: 1.4375rem;
}
.main__content__top h2 {
  color: var(--gray);
  font-size: 0.9375rem;
}
.main__content__top h1:hover, .main__content__top h2:hover {
  cursor: pointer;
}
.main__content__body {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  height: 300px;
  max-height: 300px;
  align-items: center;
  overflow: hidden;
}
.main__content__body div {
  width: 200px;
  min-width: 200px;
  background-color: var(--light-black);
  padding: 20px;
  height: 276px;
  border-radius: 10px;
  position: relative;
  transition-duration: 0.5s;
  margin: 12px 10px 12px 10px;
}
.main__content__body div img {
  width: 100%;
  border-radius: 10px;
}
.main__content__body div h1 {
  color: var(--white);
  font-size: 0.9375rem;
  margin-top: 10px;
}
.main__content__body div p {
  color: var(--gray);
  font-size: 0.9375rem;
  margin-top: 10px;
}
.main__content__body div .play {
  position: absolute;
  top: 42%;
  right: 13%;
  font-size: 25px;
  padding: 15px;
  color: black;
  background-color: var(--play-color);
  border-radius: 50%;
  opacity: 0;
}
.main__content__body div .play:hover {
  transform: scale(1.05);
}
@keyframes playanime {
  0% {
    opacity: 0;
    top: 44%;
  }
  100% {
    opacity: 100;
    top: 42%;
  }
}
.main__content__body div:hover {
  background-color: #282828;
  cursor: pointer;
}
.main__content__body div:hover .play {
  animation: playanime 0.5s;
  animation-fill-mode: forwards;
}

.right-side {
  background-color: var(--black);
  position: fixed;
  top: 0;
  right: 0;
  width: 14.375rem;
  height: 100%;
  padding: 0.9375rem;
}
@media (max-width: 58.4375em) {
  .right-side {
    display: none;
  }
}
.right-side__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.right-side__title h1 {
  color: var(--gray);
  font-family: "Roboto", sans-serif;
  font-size: 1.0625rem;
}
.right-side__title div {
  display: flex;
  align-items: center;
}
.right-side__title div ion-icon {
  margin: 0px 5px 0px 5px;
  color: var(--gray);
  font-size: 1.25rem;
}
.right-side__title div .close {
  font-size: 1.6875rem;
  margin-right: 0;
}
.right-side__content {
  color: var(--gray);
  display: flex;
  align-items: center;
  margin-top: 35px;
  font-family: "Roboto", sans-serif;
  line-height: 1.2;
  cursor: pointer;
  padding: 5px;
  transition-duration: 0.5s;
}
.right-side__content img {
  width: 22%;
  height: 22%;
  margin-right: 10px;
  border-radius: 50%;
}
.right-side__content h1 {
  font-size: 0.9375rem;
}
.right-side__content .song-name {
  display: flex;
  font-size: 0.75rem;
}
.right-side__content .artest {
  font-size: 0.75rem;
}
.right-side__content:hover {
  background-color: rgb(46, 46, 46);
  color: var(--white);
  border-radius: 10px;
}

.player {
  width: 100%;
  height: 100px;
  background-color: var(--player-color);
  position: absolute;
  bottom: 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}
@media (max-width: 58.4375em) {
  .player {
    justify-content: space-around;
  }
}
.player__left {
  display: flex;
  align-items: center;
  display: flex;
  align-items: center;
  justify-content: left;
  width: 20vw;
}
@media (max-width: 58.4375em) {
  .player__left {
    width: 25vw;
  }
}
@media (max-width: 43.4375em) {
  .player__left {
    display: none;
  }
}
.player__left img {
  width: 70px;
  margin-right: 15px;
}
.player__left div {
  line-height: 1.9;
}
.player__left div h1 {
  font-family: "Roboto", sans-serif;
  color: var(--white);
  font-size: 0.9375rem;
}
.player__left div p {
  font-family: "Roboto", sans-serif;
  color: var(--gray);
  font-size: 0.8125rem;
}
.player__left div h1:hover, .player__left div p:hover {
  text-decoration: underline;
  cursor: pointer;
}
.player__right {
  display: flex;
  align-items: center;
  justify-content: right;
  width: 20vw;
}
@media (max-width: 58.4375em) {
  .player__right {
    display: none;
  }
}
.player__right ion-icon {
  font-size: 1.4375rem;
  color: var(--gray);
  margin-left: 5px;
  margin-right: 5px;
}
.player__right ion-icon:hover {
  color: var(--white);
}
.player__right .control_volume {
  appearance: none;
  background: var(--white);
  border-radius: 5px;
  height: 4.5px;
  width: 100px;
}
.player__right .control_volume:hover {
  background: var(--play-color);
}
.player__right .control_volume::-webkit-slider-thumb {
  appearance: none;
  width: 0px;
  height: 0px;
  border-radius: 50%;
  background: var(--white);
}
.player__right .control_volume:hover::-webkit-slider-thumb {
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: var(--white);
}
.player__right .volume_div {
  height: 4.5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.player__middle {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
@media (max-width: 58.4375em) {
  .player__middle {
    width: 100%;
  }
}
.player__middle .controls {
  display: flex;
  align-items: center;
}
.player__middle .controls ion-icon {
  font-size: 1.375rem;
  margin: 0px 10px 0px 10px;
  color: var(--gray);
}
.player__middle .controls .play {
  font-size: 2.5rem;
  color: var(--white);
}
.player__middle .controls ion-icon:hover {
  color: var(--white);
}
.player__middle .track {
  display: flex;
  align-items: center;
  justify-content: center;
}
.player__middle .track p {
  color: var(--gray);
  margin: 0px 5px 0px 5px;
  font-size: 0.75rem;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
}
.player__middle .track .control_player {
  appearance: none;
  background: var(--white);
  border-radius: 5px;
  height: 4.5px;
  width: 30vw;
  margin-top: 10px;
  margin-bottom: 10px;
}
@media (max-width: 58.4375em) {
  .player__middle .track .control_player {
    width: 60vw;
  }
}
.player__middle .track .control_player::-webkit-slider-thumb {
  appearance: none;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--white);
}

.password {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: black;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}
.password .form {
  padding: 30px;
  border-radius: 10px;
  background-color: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
}
.password .form .row {
  padding: 0;
  margin: 0;
}

.add-main {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  background-color: var(--bg-color);
}
.add-main .mb-3 {
  width: 500px;
}
.add-main form {
  background-color: var(--white);
  padding: 50px;
  border-radius: 20px;
}
.add-main form button {
  width: 100%;
}
.add-main form h1 {
  text-align: center;
  margin-bottom: 10px;
}

.loader {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: var(--black);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: loader 2s forwards;
  animation-iteration-count: 1;
  animation-delay: 5s;
}
.loader img {
  width: clamp(40px, 8vw, 100px);
}
.loader h1 {
  color: var(--white);
  font-family: "Roboto", sans-serif;
  margin-left: 10px;
  font-size: clamp(18.75px, 5vw, 50px);
}
@keyframes loader {
  0% {
    opacity: 100;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

.developer {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
  display: none;
  justify-content: center;
  align-items: center;
}
.developer__main {
  width: 700px;
  height: 600px;
  background-color: var(--white);
  opacity: 100%;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0px 20px 0px 20px;
  position: relative;
  background-color: var(--black);
}
.developer__main a {
  display: flex;
  justify-content: center;
}
.developer__main img {
  width: 40%;
  border-radius: 50%;
  margin-top: -105px;
  border: 10px solid var(--black);
}
@media (max-width: 722px) {
  .developer__main img {
    margin-top: -70px;
  }
}
.developer__main .top {
  background-color: var(--play-color);
  height: 30%;
  width: 100%;
}
.developer__main .top ion-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: red;
  font-size: 1.875rem;
  border-radius: 5px;
  cursor: pointer;
}
@keyframes skake {
  0% {
    transform: translateX(-3px);
  }
  10% {
    transform: translateX(-3px);
  }
  20% {
    transform: translateX(3px);
  }
  30% {
    transform: translateX(-3px);
  }
  40% {
    transform: translateX(3px);
  }
  50% {
    transform: translateX(-3px);
  }
  60% {
    transform: translateX(3px);
  }
  70% {
    transform: translateX(-3px);
  }
  80% {
    transform: translateX(3px);
  }
  90% {
    transform: translateX(-3px);
  }
  100% {
    transform: translateX(3px);
  }
}
.developer__main .content {
  padding: 0px 20px 0px 20px;
}
.developer__main .content h1 {
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-weight: 700;
  margin-bottom: 20px;
  color: var(--bg-color);
  color: var(--white);
}
.developer__main .content p {
  font-size: clamp(0.9375rem, 2vw, 1.0625rem);
  font-family: "Roboto", sans-serif;
  color: var(--bg-color);
  font-weight: 400;
  text-align: justify;
  color: var(--white);
}
.developer__main .content p span {
  color: rgb(0, 247, 255);
}

.large_screen {
  display: none;
}

@media (max-width: 75em) {
  .large_screen {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 11;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .large_screen img {
    width: 100%;
  }
}